import { Component, h, Prop, Element, Method } from '@stencil/core';

@Component({
  tag: 'ks-promotional-popup',
  styleUrl: 'promotional-popup.css'
})
export class PromotionalPopup{
    @Element() root: HTMLElement;
    dialog: HTMLKsDialogElement;

    @Prop() name: string;
    @Prop() href: string;
    @Prop() image: string;
    @Prop() color: string = "#FFFFFF";
    @Prop() texture: string;
    @Prop() textureOpacity: string = "1.0";
    @Prop() textureSize: string = "200";

    @Prop() displayOnLoad: boolean = false;

    componentDidLoad() {
        this.dialog = this.root.querySelector("ks-dialog");
        const cookieName = this.name + "Popup";
        
        if(this.displayOnLoad && document.cookie.search(`${cookieName}=tak`) == -1) {
            setTimeout(() => {
                this.Show();
            }, 4000);
        }
    }

    @Method()
    async Show() {
        this.dialog.show();
    }

    SetCookie() {
        const cookieName = this.name + "Popup";

        var expiration = "";
        var expirationDate = new Date();
        expirationDate.setMonth(expirationDate.getMonth() + 1);
        expiration = "expires=" + expirationDate.toUTCString() + "; ";

        document.cookie = `${cookieName}=tak; ` + expiration + "path=/";
    }

    

  render() {
    const bgstyle = {
        backgroundColor: this.color
    }

    const texturestyle = {
        backgroundImage: `url("${this.texture}")`,
        backgroundSize: `${this.textureSize}px ${this.textureSize}px`,
        opacity: this.textureOpacity
    }

    return [
        <ks-dialog smallmobile dark nopadding onClosed={()=>this.SetCookie()}>
            <a href={this.href} class="info" style={bgstyle}>
                <div class="texture" style={texturestyle}></div>
                <ks-img2 src={this.image} />
            </a>
        </ks-dialog>
    ];
  }
}
